export default {
  name: 'DataTable',
  props: ['template', 'firstBlock'],
  data: function data() {
    return {
      tableData: [],
      img: [{
        name: "TemperatureCurve",
        value: "http://cdn.gosafenet.com/static/board/goodDtails/temp.png"
      }, {
        name: "BoardVoltage",
        value: "http://cdn.gosafenet.com/static/board/goodDtails/vol.png"
      }, {
        name: "BoardCurrent",
        value: "http://cdn.gosafenet.com/static/board/goodDtails/amp.png"
      }, {
        name: "BoardVexFlow",
        value: "http://cdn.gosafenet.com/static/board/goodDtails/flow.png"
      }, // 流量曲线
      {
        name: "BoardPressure",
        value: "http://cdn.gosafenet.com/static/board/goodDtails/yali.png"
      }, {
        name: "Flow",
        value: "http://cdn.gosafenet.com/static/board/goodDtails/zendong.png"
      }, {
        name: "Shake1Flow",
        value: "http://cdn.gosafenet.com/static/board/goodDtails/zendong.png"
      }]
    };
  },
  watch: {
    template: function template() {
      this.init();
    }
  },
  mounted: function mounted() {
    this.init();
  },
  methods: {
    init: function init() {
      var _this = this;

      this.template.map(function (item, index) {
        if (item.viewCode == "Status") {
          _this.template.splice(index, 1);
        }

        if (_this.firstBlock == item) {
          _this.$nextTick(function () {
            _this.$refs['colmns' + index][0].style.background = '#383e90'; // 给首次显示的行添加自己的颜色
          });
        }
      });
      this.template.map(function (item, index) {
        // 将频谱图放在一项中 声音/加速度
        if (item.viewCode == 'BoardSound') {
          _this.template.splice(index, 1);
        }

        if (item.viewCode == 'BoardVibration') {
          _this.template.splice(index, 1);

          _this.template.push({
            viewName: '振动音频',
            viewCode: 'Flow'
          });
        } // 将频谱图放在一项中 听诊器声音/加速度


        if (item.viewCode == 'BoardShake1Audio') {
          _this.template.splice(index, 1);
        }

        if (item.viewCode == 'BoardShake1Acc') {
          _this.template.splice(index, 1);

          _this.template.push({
            viewName: '听诊器振动音频',
            viewCode: 'Shake1Flow'
          });
        } // 将对应模块列表的图片放入对象中


        _this.img.map(function (i) {
          if (item.viewCode == i.name) {
            item['img'] = i.value;
          }
        });
      });
      this.tableData = this.template;
    },
    choose: function choose(row, index) {
      // 点击后传给父组件
      this.$emit('tableRow', row);

      for (var i = 0; i < this.tableData.length; i++) {
        // 将所有的行颜色去除
        this.$refs['colmns' + i][0].style.background = 'none';
      }

      this.$refs['colmns' + index][0].style.background = '#383e90'; // 给当前点击的行添加自己的颜色
    }
  }
};