import echarts from 'echarts';
import BoardVibration from '@/components/boardVibration'; // 声音频谱图

import BoardShake1Acc from '@/components/boardShake1Acc'; // 听诊器声音频谱图

import { swiper, swiperSlide } from 'vue-awesome-swiper';
export default {
  name: 'DataDetail',
  props: ['dataNum'],
  data: function data() {
    return {
      myChart: null,
      swiperOption: {
        grabCursor: true,
        // 针会变成手掌形状
        setWrapperSize: true,
        slidesPerView: 6,
        // 一行显示6个
        slidesPerGroup: 1 // 一次滚动一个
        // pagination: {
        //   el: '.swiper-pagination',
        //   progressbarOpposite: true,
        //   type: 'fraction',
        // },

      },
      unit: '' // 单位

    };
  },
  watch: {
    dataNum: function dataNum() {
      this.init();
    }
  },
  components: {
    BoardVibration: BoardVibration,
    BoardShake1Acc: BoardShake1Acc,
    swiper: swiper,
    swiperSlide: swiperSlide
  },
  created: function created() {
    this.init();
  },
  methods: {
    init: function init() {
      var _this = this;

      if (this.dataNum.constructor == Object) {
        return;
      } // 如果是频谱图return


      this.$nextTick(function () {
        _this.dataNum.map(function (item, index) {
          _this.unit = item.unit; // 赋值单位
          // var num = Number(item.maxNumber) + Number(item.maxNumber)/2 // 总数 100%
          // var num1 = (Number(item.minNumber) / num) // 最小值第一节点
          // var num2 = (Number(item.maxNumber) / num) // 最大值第二节点
          // console.log(item.maxNumber, item.minNumber,num, Number(num1.toFixed(2)), Number(num2.toFixed(2)))

          var option = {
            backgroundColor: 'rgb(29,36,87)',
            title: {
              show: true,
              x: "center",
              bottom: 5,
              text: item.fieldName,
              textStyle: {
                fontWeight: 'normal',
                fontSize: 13,
                color: "#42bdee"
              }
            },
            series: [{
              name: '灰色内圈',
              //刻度背景
              type: 'gauge',
              radius: '90%',
              startAngle: 180,
              endAngle: 0,
              center: ["50%", "60%"],
              //整体的位置设置
              axisLine: {
                // 坐标轴线
                lineStyle: {
                  width: 3,
                  shadowBlur: 0,
                  color: [[0.3, '#67e0e3'], [0.7, '#37a2da'], [1, '#fd666d']]
                }
              },
              max: 15,
              min: 5,
              splitLine: {
                show: false
              },
              axisLabel: {
                show: false
              },
              pointer: {
                show: false
              },
              axisTick: {
                show: false
              },
              detail: {
                show: 0
              }
            }, {
              name: '指针',
              type: 'gauge',
              z: 6,
              radius: '95%',
              startAngle: 180,
              // 仪表盘指针最大值
              endAngle: 0,
              center: ["50%", "60%"],
              //整体的位置设置
              axisLine: {
                lineStyle: {
                  // 属性lineStyle控制线条样式//指针颜色
                  // color: [[1, '#33ffff']],  
                  width: 0
                }
              },
              axisTick: {
                show: false
              },
              splitLine: {
                show: false
              },
              axisLabel: {
                show: false
              },
              data: [item.fieldValue == 215 || item.fieldValue == 255 ? null : item.fieldValue],
              //指针位置
              pointer: {
                show: true,
                width: 2,
                length: '55%'
              },
              detail: {
                show: 0
              }
            }, {
              name: '外层盘',
              type: 'gauge',
              z: 6,
              radius: '100%',
              startAngle: 180,
              endAngle: 0,
              center: ["50%", "60%"],
              //整体的位置设置
              axisLine: {
                lineStyle: {
                  // 属性lineStyle控制线条样式//控制外圈位置
                  color: [[0.5, 'rgb(120, 191, 200)'], [1, 'rgba(255, 255, 255, 0.2)']],
                  width: 2,
                  opacity: 0.8 //控制外圈位置，颜色，宽度，透明度

                }
              },
              axisTick: {
                show: false
              },
              splitLine: {
                show: false
              },
              axisLabel: {
                show: false
              },
              pointer: {
                show: false
              },
              data: [{
                show: false,
                value: '10'
              }],
              detail: {
                show: 0
              }
            }]
          };

          _this.$nextTick(function () {
            _this.myChart = echarts.init(_this.$refs['myChart' + index][0]);

            _this.myChart.setOption(option, true); //  echart 重绘

          });
        });
      });
    }
  }
};