import { BoardHeader } from '@/components';
import CurveData from "./curveData"; // 曲线展示

import BoardBox from '@/components/boardBox';
import DataTable from "./dataTable";
import DataDetail from "./dataDetail";
import { GetUtcTime } from '@/api/equipmentManagement/status'; // 数据接口

import { GetViewByDeviceId, GetByDeviceIdAndViewId, GetData } from '@/api/devicelist';
import { buttonPermissions, resize } from '@/mixin';
export default {
  name: 'good',
  components: {
    BoardHeader: BoardHeader,
    BoardBox: BoardBox,
    DataTable: DataTable,
    CurveData: CurveData,
    DataDetail: DataDetail
  },
  mixins: [buttonPermissions, resize],
  data: function data() {
    return {
      viewTheme: undefined,
      activeHome: true,
      connected: '',
      RunYear: '',
      status: '',
      // 运行状态
      ElectricityConsum: '',
      template: [],
      viewId: null,
      firstBlock: {},
      // 第一个曲线模块的对象
      curData: [],
      // 曲线的数据
      dataNum: [],
      timer: null,
      statusId: '' // 设备状态的id

    };
  },
  created: function created() {
    var _this = this;

    this.viewTheme = this.$store.state.user.config.viewTheme; // deviceId: this.$route.query.id, viewTypeId: 200

    GetViewByDeviceId({
      deviceId: this.$route.query.id,
      viewTypeId: 200
    }).then(function (res) {
      // 根据当前的设备id 获取有哪些板块
      _this.template = res.data;
      res.data.map(function (item) {
        if (item.viewCode == "Status") {
          _this.statusId = item.viewId; // 设备状态的id

          _this.template.splice(item, 1); // 模版中去除有状态的哪一项


          _this.firstBlock = _this.template[0];

          _this.getStatus(item.viewId); // 状态的数据

        }
      });

      _this.getCurveData();
    });
  },
  mounted: function mounted() {
    var _this2 = this;

    this.timer = setInterval(function () {
      _this2.getCurveData();
    }, 7000);
  },
  beforeDestroy: function beforeDestroy() {
    clearInterval(this.timer);
  },
  methods: {
    hiddleClickBtnData: function hiddleClickBtnData() {
      this.$router.push({
        path: "/boardData",
        query: {
          id: this.$route.query.id,
          name: this.$route.query.name,
          code: this.$route.query.code,
          come: 'boardDetailGood'
        }
      });
    },
    tableRow: function tableRow(data) {
      var _this3 = this;

      if (data.viewCode == "Flow" || data.viewCode == "Shake1Flow") {
        // "声音频谱图" // "振动加速度"
        clearInterval(this.timer); // 清除请求数据的定时器

        this.timer = null;
        this.dataNum = data;
      } else {
        this.viewId = data.viewId;
        this.getCurveData();

        if (this.timer == null) {
          this.timer = setInterval(function () {
            // 打开请求数据的定时器
            _this3.getCurveData();
          }, 7000);
        }
      }
    },
    getStatus: function getStatus(viewId) {
      var _this4 = this;

      GetByDeviceIdAndViewId({
        deviceId: this.$route.query.id,
        viewId: viewId
      }).then(function (res) {
        _this4.connected = res.data[0].contected;

        if (viewId == _this4.statusId) {
          // 如果是设备状态的话
          res.data[0].fields.map(function (item) {
            if (item.fieldCode == "YearHour") {
              // 运行时长
              _this4.RunYear = Number(item.fieldValue).toFixed(0);
            } else if (item.fieldCode == "RunYear") {
              _this4.RunYear = Number(item.fieldValue).toFixed(0);
            } else if (item.fieldCode == "Motor_Status") {
              // 连接状态
              _this4.status = Number(item.fieldValue).toFixed(0);

              if (_this4.status == 0) {
                _this4.status = '连接';
              }
            } else if (item.fieldCode == "Power_Supply") {
              // 连接状态 Power_Supply
              _this4.status = Number(item.fieldValue).toFixed(0);

              if (_this4.status == 0) {
                _this4.status = '连接';
              }
            } else if (item.fieldCode == "ElectricityConsum") {
              // 耗电量
              _this4.ElectricityConsum = Number(item.fieldValue).toFixed(0);
            }
          });
        } else {
          // 是设备指标 就传递给曲线和实时数据
          _this4.dataNum = res.data[0].fields; // 每个指标类型下的指标个数数组 设备状态类
        }
      });
    },
    getCurveData: function getCurveData() {
      var _this5 = this;

      // 获取曲线数据
      if (this.viewId == null) {
        this.viewId = this.firstBlock.viewId;
      }

      this.getStatus(this.viewId); // 指标的数据

      var reqTime = '';
      GetUtcTime().then(function (res) {
        reqTime = res.data.utcTime.replace(/-/g, '/');
        GetData({
          deviceId: _this5.$route.query.id,
          viewId: _this5.viewId,
          startTime: _this5.$moment(new Date(new Date(reqTime).getTime() - 1800000)).format('YYYY-MM-DD hh:mm:ss'),
          // 开始的时间
          pageIndex: 1,
          pageSize: 200
        }).then(function (res) {
          _this5.curData = res.data.items;
        });
      });
    }
  }
};