import echarts from 'echarts';
import BoardSound from '@/components/boardSound';
import BoardShake1Audio from '@/components/boardShake1Audio';
export default {
  name: 'CurveData',
  components: {
    BoardSound: BoardSound,
    BoardShake1Audio: BoardShake1Audio
  },
  data: function data() {
    return {
      timer: null,
      charts: {
        unit: '℃',
        names: [],
        lineX: [],
        value: []
      },
      myChart: null
    };
  },
  props: ['dataNum', 'curData'],
  watch: {
    curData: function curData() {
      this.getData();
    },
    dataNum: function dataNum() {
      this.getData();
    }
  },
  created: function created() {
    this.getData();
  },
  methods: {
    getData: function getData() {
      var _this = this;

      if (this.dataNum.constructor == Array) {
        var arr = []; // 最后数值存放的数组

        this.dataNum.map(function (i) {
          arr.push({
            name: i.fieldCode,
            value: _this.curData.map(function (item) {
              return Number(item.data[i.fieldCode]) == 255 || Number(item.data[i.fieldCode]) == 215 ? undefined : Number(item.data[i.fieldCode]).toFixed(2);
            })
          });
        });
        var X = this.curData.map(function (item) {
          return _this.$moment.utc(item.time).local().format('YYYY-MM-DD HH:mm:ss');
        });
        this.$set(this.charts, 'lineX', X.reverse()); // 获取x轴的值

        arr.map(function (item, index) {
          _this.$set(_this.charts.value, index, item.value.reverse());
        });
        this.init();
      } else {
        return;
      }
    },
    init: function init() {
      var _this2 = this;

      // this.myChart = null
      this.$nextTick(function () {
        var dom = _this2.$refs.echarts;
        _this2.myChart = echarts.init(dom);
      });
      var lineY = [];
      var color = ['#17FFF3', '#4FAD32', '#278db5', '#FFFFC8', '#b95f5f', '#FFFF82', '#39c59f', '#e08ab9', '#e0945c', '#bc83d8'];

      for (var i = 0; i < this.dataNum.length; i++) {
        var legendData = [];
        this.dataNum.map(function (item) {
          legendData.push(item.fieldName);
        });
        var x = i;

        if (x > color.length - 1) {
          x = color.length - 1;
        }

        var data = {
          name: this.dataNum[i].fieldName,
          type: 'line',
          color: color[x],
          markLine: {
            //最大值和最小值 y轴的标注点  有几条线就会生成几个点, 一样会重叠
            symbol: 'none',
            // 去掉箭头
            data: [{
              name: '上限值',
              yAxis: this.dataNum[i].maxNumber,
              label: {
                show: 'true',
                position: 'left',
                fontSize: 2 // formatter: '上限 - ' + this.dataNum[i].fieldName

              },
              lineStyle: {
                normal: {
                  width: 0,
                  color: color[x]
                }
              },
              axisLabel: {
                textStyle: {
                  color: '#000',
                  fontSize: 0
                }
              },
              symbol: 'circle'
            }, {
              name: '下限值',
              yAxis: this.dataNum[i].minNumber,
              label: {
                show: 'true',
                position: 'left',
                fontSize: 2 // formatter: '下限 - ' + this.dataNum[i].fieldName

              },
              lineStyle: {
                normal: {
                  width: 0,
                  color: color[x]
                }
              },
              symbol: 'circle'
            }]
          },
          smooth: true,
          symbol: 'circle',
          symbolSize: 4,
          data: this.charts.value[i]
        };
        lineY.push(data);
      }

      var option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            // 鼠标触发后的标注线
            lineStyle: {
              color: '#57617B'
            }
          }
        },
        legend: {
          // 提示处样式 字体
          // icon: '',
          itemGap: 13,
          data: legendData,
          top: '-1%',
          left: 'center',
          textStyle: {
            fontSize: 18,
            color: '#F1F1F3'
          }
        },
        xAxis: [{
          type: 'category',
          boundaryGap: false,
          axisLine: {
            lineStyle: {
              color: '#17fff3'
            }
          },
          data: this.charts.lineX
        }],
        yAxis: [{
          type: 'value',
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: '#17fff3'
            }
          },
          axisLabel: {
            // margin: 10,
            textStyle: {
              color: "#17fff3",
              fontSize: '14'
            }
          },
          splitLine: {
            lineStyle: {
              color: "rgba(255,255,255,0.1)"
            }
          }
        }],
        series: lineY
      };
      this.$nextTick(function () {
        // this.myChart = echarts.init(this.$refs.echarts)
        _this2.myChart.setOption(option, true); //  echart 重绘

      });
    }
  }
};